.basic__header {
    -webkit-box-shadow: 2.5px 4.33px 5px rgba(246,248,251,0.75);
            box-shadow: 2.5px 4.33px 5px rgba(246,248,251,0.75);
    background-color: #ffffff;
    width: 100%;
}

.basic__header-content {
    height: 70px;
    max-width: 1200px;
    margin: 0 auto;
    margin-top: 35px;
}

.header__content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.header__container {
    position: relative;
}

.left__content-icons {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.add__work_wrapper {
    padding-left: 50px;
}


.social__icons {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-align: center;
            align-items: center;
}

.social__icon-instagram::before,
.social__icon-instagram::after,
.social__icon-behance::before,
.social__icon-behance::after{
    cursor: pointer;
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 100%;
    -webkit-transition: 0.35s cubic-bezier(0.62, 0.02, 0.34, 1);
    -o-transition: 0.35s cubic-bezier(0.62, 0.02, 0.34, 1);
    transition: 0.35s cubic-bezier(0.62, 0.02, 0.34, 1);
    will-change: opacity;
}

.check__work {
    cursor: pointer;
}

.social__icon-instagram,
.social__icon-behance {
    width: 30px;
    height: 30px;
    position: relative;
}

.social__icon-instagram {
    margin-left: 15px;
}


.social__icon-instagram:hover::before,
.social__icon-behance:hover::before{
    opacity: 0;
    -webkit-transition-delay: 0.1s;
    -o-transition-delay: 0.1s;
    transition-delay: 0.1s;
}


.social__icon-instagram::before {
    background-image: url('../../sources/sprites/icons/inst-icon.svg');
}

.social__icon-instagram::after {
    background-image: url('../../sources/sprites/icons/inst-icon-hover.svg');
    opacity: 0;
    -webkit-transition-delay: 0.2s;
    -o-transition-delay: 0.2s;
    transition-delay: 0.2s;
}

.social__icon-behance::before{
    background-image: url('../../sources/sprites/icons/behance.svg');
}

.social__icon-behance::after {
    background-image: url('../../sources/sprites/icons/behance-icon-hover.svg');
    opacity: 0;
    -webkit-transition-delay: 0.2s;
    -o-transition-delay: 0.2s;
    transition-delay: 0.2s;
}

.social__icon-instagram:hover::after,
.social__icon-behance:hover::after{
    opacity: 1;
    -webkit-transition-delay: 0s;
    -o-transition-delay: 0s;
    transition-delay: 0s;
}


.social__icon-instagram:hover,
.social__icon-behance:hover {
    -webkit-animation: flash-soft 0.4s forwards linear 0.18s;
    animation: flash-soft 0.4s forwards linear 0.18s;
}



.social__icon-behance {
    cursor: pointer;
}


.main__header {
    background-color: #f3f3f5;
    width: 100%;
    position: absolute;
    top: 70px;
    -webkit-animation: showNav 250ms ease-in-out 0s 1 normal both running;
            animation: showNav 250ms ease-in-out 0s 1 normal both running;
    z-index: 1;
}


.main__header-left {
    display: -ms-flexbox;
    display: flex;
    height: 263px;
    -ms-flex-align: center;
        align-items: center;
}

.link__menu {
    color: #0a0a0a;
    font-family: "MontBold";
    font-size: 24px;
    font-weight: 400;
    line-height: 9.87px;
    text-decoration: none;
}

.description__menu-title {
    color: #0a0a0a;
    font-family: "Montserrat";
    font-size: 14px;
    font-weight: 500;
    line-height: 9.87px;
}

.main__header-content {
    max-width: 1200px;
    margin: 0 auto;
}

.menu__header-item:first-child {
    margin-bottom: 60px;
}

.menu__header-column div {
    margin-right: 100px;
}

@-webkit-keyframes showNav {
    from {opacity: 0;}
    to {opacity: 1;}
}

@keyframes showNav {
    from {opacity: 0;}
    to {opacity: 1;}
}


