.brands__company-container {
    background: #171817;
    width: 100%;
}

.brands__company {
    max-width: 1200px;
    min-width: 1200px;
    margin: 0 auto;
}

.brand__content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    padding-top: 60px;
    padding-bottom: 60px;
}



.title__brand .title {
    font-family: MontBold;
    font-size: 30px;
    font-weight: 300;
    margin: 0;
    color: #ffffff;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
}

.title:hover {
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    cursor: pointer;
    background-image: -webkit-gradient(linear, left top, right top, from(#d99b91), to(#ad6f59));
    background-image: -webkit-linear-gradient(left, #d99b91 0%, #ad6f59 100%);
    background-image: -o-linear-gradient(left, #d99b91 0%, #ad6f59 100%);
    background-image: linear-gradient(90deg, #d99b91 0%, #ad6f59 100%);
}

.content__brands {
    margin-top: 60px;
}

.brand img {
    max-width: 100%;
    min-width: 100%;
    height: auto;
    opacity: 0.6;
    -webkit-transition: opacity  0.6s;
    -o-transition: opacity  0.6s;
    transition: opacity  0.6s;
}

.brand img:hover{
    opacity: 1;
}



.brands {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.brand {
    width: 70px;
    -ms-flex-preferred-size: 25%;
        flex-basis: 25%;
    margin-top: 27px;
    margin-bottom: 10px;
}




